.header {
  background-color: $hero-bgr-color;
  overflow: auto;
}
@media screen and (min-width: 320px) {
  .header {
    overflow: hidden;
  }
}
.hero__header__container {
  position: relative;
  height: 502px;
  max-width: 320px;
  padding: 0 20px;
  padding-top: 6px;
  background-color: $hero-bgr-color;
  margin-left: auto;
  margin-right: auto;
}
.logo {
  width: 199px;
  height: 50px;
  background-color: inherit;
}
.button__open {
  z-index: 10;
  display: flex;
  background-color: inherit;
  border: none;
  padding: 0;
}
.header__contain {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.hero {
  position: absolute;
  margin-top: 137px;
  width: 218px;
  height: 106px;
  z-index: 99;
}
.hero__title {
  font-family: $secondary-font;
  font-size: 26px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: $hero-text-color;
}
.hero__title--accent {
  font-size: 48px;
  line-height: 96.5%;

  color: $accent-color;
}

.button__contain {
  display: flex;
  margin-top: 19px;
}

.button__products {
  font-family: $primary-font;
  font-size: 8px;
  line-height: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $accent-color;
  color: $hero-text-color;
  border-radius: 20px;
  border-color: transparent;

  width: 62.4px;
  height: 24px;
}
.button__made {
  margin-left: 8px;
  font-family: $primary-font;
  font-size: 8px;
  line-height: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  color: $accent-color;
  width: 82.8px;
  height: 24px;
  border-color: transparent;
}

.slider {
  width: 171px;
  height: 410px;
  position: absolute;
  top: 9px;
  left: 81px;
  z-index: 50;
}
.slider__bgc {
  position: absolute;
  width: 346px;
  height: 344px;
  left: 36px;
  top: 83px;
  z-index: 0;
  background: $tertiary-text-color;
  border-radius: 50%;
}
.modal {
  position: fixed;
  width: 250px;
  height: 100vh;
  right: 0;
  top: 0;
  z-index: 200;

  background: $modal-bgr-color;

  transform: translateX(100%);
}

.button__close {
  position: absolute;
  right: 28px;
  top: 23px;
}
.buy__now {
  z-index: 10;

  padding: 0;
  width: 209px;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;
  display: flex;
  justify-content: center;

  color: $accent-color;

  background: $hero-text-color;
  border-radius: 22px;
  border: transparent;
}
.button__in__modal {
  position: absolute;
  left: 21px;
  top: 60px;
}
.buy__now--contain {
  display: flex;
  align-items: center;
}
.buy__now__icon {
  margin: 0;
  margin-left: 14px;
}
.buy__now--content {
  justify-content: center;
  display: flex;
  width: 90.7px;
  height: 44px;
  align-items: center;
}
.modal__list {
  position: absolute;
  background: $modal-bgr-color;
  left: 0;
  top: 136px;
  list-style: none;
  padding: 0;
}
.modal__item {
  left: 0px;
  top: 123px;
  border: 1px solid $modal-border-color;
  padding-left: 21px;
}
.modal__link {
  width: 227px;
  height: 42px;

  font-family: $primary-font;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.286;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: $hero-text-color;
}
.modal__link.modal__link--current {
  color: $accent-color;
}
.is-open {
  transform: translateX(0);
}
@media screen and (max-width: 767px) {
  .button__in__tablet {
    display: none;
  }
  .bio__eco {
    display: none;
  }
  .example__eat__ice-cream {
    display: none;
  }
  .goods {
    display: none;
  }
}
@media screen and (min-width: 768px) {
  .modal {
    width: 254px;
  }
  .hero__header__container {
    padding: 0 34px;
    padding-top: 37px;
    max-width: 768px;
    height: 399px;
  }
  .hero {
    margin-top: 15px;
  }
  .hero__title {
    font-size: 22px;
    line-height: 1.136;
  }
  .hero__title--accent {
    font-size: 34px;
    line-height: 90.5%;
  }
  .button__contain {
    margin-top: 20px;
  }
  .slider__bgc {
    left: 307px;
    top: 31px;
  }
  .slider {
    width: 143px;
    height: 336px;
    top: 32px;
    left: 71px;
  }
  .header__button__position {
    display: flex;
    align-items: center;
  }
  .button__open {
    margin-right: 30px;
  }
  .button__in__modal {
    left: 34px;
  }
  .button__in__tablet {
    top: 38px;
    left: 574px;
    width: 160px;
    height: 44px;
    align-items: center;
  }
  .buy__now--contain--tablet {
    display: flex;
    align-items: center;
  }
  .buy__now__icon--tablet {
    margin-left: 12px;
  }
  .buy__now--content--tablet {
    display: flex;
    align-items: center;
  }
  .bio__eco--image {
    width: 124px;
    height: 126px;

    position: absolute;
    left: 34px;
    top: 245px;
  }
  .bio__eco__description {
    position: absolute;
    left: 154px;
    top: 292px;
    width: 114px;
    height: 48px;

    font-size: 8px;
    line-height: 150%;
    color: #ffffff;
  }
  .bio__eco--icon {
    position: absolute;
    left: 154px;
    bottom: 26px;
    width: 18px;
    height: 18px;
  }
  .goods {
    position: absolute;
    left: 691px;
    top: 96px;
    .goods__list {
      position: relative;
      list-style: none;
      padding: 0;
      margin: 0;
    }
    .goods__item {
      position: absolute;
      z-index: 30;
      &:not(:last-child) {
        left: 0px;
        top: 0px;
      }
      &:last-child {
        left: 0px;
        top: 50px;
      }
    }
    .among {
      font-family: Titan One;
      font-size: 28px;
      line-height: 1.14;
      letter-spacing: 0.04em;

      color: #d41443;
    }
    .goods__name {
      width: 50px;
      display: block;
      justify-content: center;
      display: flex;
      font-family: DM Sans;
      font-size: 8px;
      line-height: 1.25;

      color: #ffffff;
    }
  }
  .back__style {
    width: 16px;
    height: 16px;
    background: #ffffff;
    border-radius: 50%;
    transform: matrix(-1, 0, 0, 1, 0, 0);
  }
  .back__style-first {
    position: absolute;
    left: 715px;
    top: 94px;
  }
  .back__style-second {
    position: absolute;
    left: 715px;
    top: 144px;
  }
  .example__eat__ice-cream {
    position: absolute;
    width: 168px;
    height: 133px;
    left: 566px;
    top: 266px;
  }
}
@media screen and (max-width: 1199px) {
  .site-nav__list {
    display: none;
  }
}
@media screen and (min-width: 1200px) {
  .modal {
    display: none;
  }
  .hero__header__container {
    padding: 0 46px;
    padding-top: 37px;
    max-width: 1280px;
    height: 665px;
  }
  .button__open {
    display: none;
  }
  .button__in__tablet {
    left: 900px;
    width: 159px;
  }
  .site-nav__list {
    z-index: 90;

    margin: 0;
    padding: 0;
    display: flex;
    list-style: none;
  }
  .site-nav__item + .site-nav__item {
    margin-left: 35px;
  }
  .site-nav__link {
    text-decoration: none;
    font-family: DM Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 21px;
    display: flex;
    align-items: center;
    color: #ffffff;
  }
  .site-nav__link--current {
    color: #d41443;
  }
  .hero {
    left: 115px;
    top: 171px;
    width: 277px;
    height: 137px;
  }
  .hero__title {
    font-size: 38px;
    line-height: 1.158;
  }
  .hero__title--accent {
    font-size: 58px;
  }
}
